import {NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes : Routes = [
    {
        path : 'manageCountry',
        loadChildren : () => import('./manage-country/manage-country.module').then(m=>m.ManageCountryModule),
        data : {title : 'CoprateUser'}
    },
	 {
        path : 'manageState',
        loadChildren : () => import('./manage-state/manage-state.module').then(m=>m.ManageStateModule),
        data : {title : 'CoprateUser'}
    },
	 {
        path : 'manageCity',
        loadChildren : () => import('./manage-city/manage-city.module').then(m=>m.ManageCityModule),
        data : {title : 'CoprateUser'}
    },
    {
        path : 'manageStatePin',
        loadChildren : () => import('./manage-state-pin/manage-state-pin.module').then(m=>m.ManageStatePinModule),
        data : {title : 'manageStatePin'}
    }
     
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class ManageGeographicsRoutingModule{}